import cookie from '@/utils/cookie.js';
import { MapItem } from '@/types/types';

const { domain, configToken, linkLoginUrl } = process['CONFIG'];

export function getSecTime(timeRange: [string, string]): [number, number] {
  const getSec = (time: string) => Math.floor(Date.parse(time) / 1000); // 后端按秒计算

  return [getSec(timeRange[0]), getSec(timeRange[1])];
}

export function quitSystem() {
  cookie.remove(configToken, { domain });
  // 判断是不是在iframe里边
  if (window.frames.length !== window.parent.frames.length) {
    window.parent.postMessage({ logout: true }, '*');
  }
  window.location.href = `${ linkLoginUrl }?url=${ encodeURIComponent(location.pathname) }`;
}

// 处理请求参数，移除不存在的数据
export function getRequestParams(params: any) {
  const formatParams = {};

  for (const key in params) {
    if (params[key] !== undefined) {
      formatParams[key] = params[key];
    }
  }

  return formatParams;
}

// 处理请求参数
export function getRequestParamsString(params: any) {
  const paramsArr = Object.keys(params).filter((r: string) => params[r]).map((key: string) => {
    if (params[key]) {
      return `${ key }=${ params[key] }`;
    }
  });

  return paramsArr.join('&');
}

export function addAll(list: MapItem[]) {
  const arr = [...list];
  const index = arr.findIndex(({ value, label }: MapItem) => label === '全部');

  if (index < 0) {
    list.unshift({ value: '', label: '全部' });
  }

  return arr;
}

export function TransKvData(list: any[], key: string, value: string) {
  const kvData = {};

  list.forEach((item: any) => {
    kvData[item[key]] = item[value];
  });

  return kvData;
}

export function TransAdventureKvData(list: any[], value: string, ...keys: string[]) {
  const kvData = {};

  list.forEach((item: any) => {
    const longKey = keys?.reduce((pre: string, key: string) => (pre + item[key]), '');

    kvData[longKey] = item[value];
  });

  return kvData;
}

export function getBase64(file: File) {
  return new Promise((resolve: any, reject: any,) => {
    const reader = new FileReader();

    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error: any) => reject(error);
  });
}

export function backTop() {
  const currentScroll = document.documentElement.scrollTop || document.body.scrollTop;

  if (currentScroll > 0) {
    // window.requestAnimationFrame(smoothscroll);
    window.scrollTo (0, 0);
  }
}