import { useRef } from 'react';
import useOldSWR, { ConfigInterface } from 'swr';
import request, { Options as RequestOptions } from '../utils/request';

interface Options extends ConfigInterface {
  requestParams?: RequestOptions;
}


const defaultOption = {
  fetcher: request,
  revalidateOnFocus: false,
  shouldRetryOnError: false
};

const useSWR = <T>(params1: any, configs?: Options) => {
  const prevData = useRef<any>();
  const { requestParams, ...option } = configs || {};
  const config = { ...defaultOption, ...option };

  if (requestParams) {
    // 为fetcher加入参数
    config.fetcher = (url: string) => request(url, requestParams);
  }

  const { data, isValidating, ...rest } = useOldSWR<T | undefined>(
    params1,
    config
  );

  // data为空时, 不重复渲染, 避免表格突然没数据
  if (data) {
    prevData.current = data;
  }

  return { data: prevData.current, isValidating, ...rest };
};

export default useSWR;
