import React, { FC, useCallback, useState, useRef, useMemo } from 'react';
import { Link } from 'umi';
import moment from 'moment';
import QueryTable, {
  OnSearch,
  Props as QueryTableProps,
} from '@/components/QueryTable';
import { useGetProductionList } from './services';
import { withAuthorized } from '@/utils/Authorized';
import { Role, getAuthority } from '@/utils/authority';
import { Button } from 'antd';
import { PlusSquareOutlined } from '@ant-design/icons';
import {
  FilmTypeOptions,
  VisiableOptionsMaps,
  FilmStatusMaps,
  FilmTypeOptionsMaps,
  FilmStatus,
  VisiableOptions,
} from '@/utils/constant.js';
import PreviewImg from '@/components/PreviewImg';
import ShouCang from './components/shoucang';
import ActionButtons from './components/Action';
import { ProductionItem } from './types';
import style from './index.less';
import CopyId from '@/components/CopyId';

const PAGESIZE = 10;
const formItemLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const textShortItemLayout = {
  labelCol: { span: 5 },
  wrapperCol: { span: 19 },
};
const authority = getAuthority();
const hasPushBtn =
  authority.indexOf(Role.SYSTEM) >= 0 || authority.indexOf(Role.WORKS) >= 0;

const SORT_TYPE = 'desc';
const initSearch = {
  pagination: { pageSize: PAGESIZE, current: 1 },
  query: {},
  sort: { type: SORT_TYPE, key: 'create_time' },
};

const Production: FC = () => {
  const formRef = useRef<any>();
  const queryTable = useRef<any>();
  const [requestBody, setRequestBody] = useState(initSearch);
  const [selectTimeVisible, setSelectTimeVisible] = useState(false);
  const [targetProduction, setTargetProduction] = useState<ProductionItem>({});

  const {
    productionList,
    pagination,
    isValidating,
    mutate,
  } = useGetProductionList(requestBody);

  const handleShow = (record: ProductionItem) => {
    setSelectTimeVisible(true);
    setTargetProduction(record);
  };

  const columns = [
    {
      title: '片单名称',
      dataIndex: 'title',
      render: (text: string, record: ProductionItem) => (
        <div>
          <Link
            to={`/content-manage/filmList-manage/add?dramaId=${record.listId}`}
          >
            <p>{text}</p>{' '}
          </Link>
          <CopyId
            text={record?.listId}
            iconStyle={{
              marginLeft: 8,
              color: '#1890ff',
              verticalAlign: 'middle',
            }}
          />
        </div>
      ),
    },
    {
      title: '片单类型',
      dataIndex: 'type',
      render: (text: string) => FilmTypeOptions[text],
    },
    {
      title: '海报缩略图',
      dataIndex: 'coverImageUrl',
      render: (text: string) => {
        return text ? <PreviewImg imgUrls={text} placement="right" /> : <p />;
      },
    },
    {
      title: '作品数',
      dataIndex: 'dramaTotal',
      render: (text: string) => Number(text) || 0,
      sorter: (a, b) => a.dramaTotal - b.dramaTotal,
    },
    {
      title: '收藏量',
      dataIndex: 'followTotal',
      render: (text: string, record) => {
        return (
          <>
            {Number(text) || 0}{' '}
            <PlusSquareOutlined onClick={() => handleShow(record)} />
          </>
        );
      },
      sorter: (a, b) => a.followTotal - b.followTotal,
    },
    {
      title: '评论量',
      dataIndex: 'commentTotal',
      render: (text: string) => Number(text) || 0,
    },
    {
      title: '隐私设置',
      dataIndex: 'visibility',
      render: (text: string, record: ProductionItem) => VisiableOptions[text],
    },
    {
      title: '发布账号',
      dataIndex: 'nickName',
      render: (text, record) => {
        return (
          <>
            {record.type == 1 ? (
              text
            ) : (
              <Link to={`/user-manage/user/${record?.creatorId}`}>{text}</Link>
            )}
          </>
        );
      },
    },
    {
      title: '更新时间',
      dataIndex: 'updateTimeLong',
      render: (text) => {
        return (
          <span>{moment(Number(text)).format('YYYY-MM-DD HH:mm:ss')}</span>
        );
      },
      // align: 'center',
      // render: (text: number, record: ProductionItem) => {
      //   const isNewProduction = getIsNewProduction(text);

      //   return (
      //     <>
      //       {isNewProduction ? <p style={{ color: 'red' }}>是</p> : <p>否</p>}
      //       <p>
      //         <Countdown time={ record?.topDeadline }
      //           style={{ fontSize: '14px', color: 'red' }}
      //           overTimeText=""
      //         />
      //       </p>
      //       {hasPushBtn ? (
      //         <Button type="primary"
      //           size="small"
      //           onClick={ () => handleShow(record) }
      //         >
      //           新片置顶
      //         </Button>
      //       ) : (
      //         ''
      //       )}
      //     </>
      //   );
      // },
    },
    {
      title: '更新操作人',
      dataIndex: 'operator',
    },
    {
      title: '审核状态',
      dataIndex: 'auditStatus',
      render: (text) => {
        return (
          <span>{text == 0 ? '待审核' : text == 100 ? '通过' : '不通过'}</span>
        );
      },
    },
    {
      title: '状态',
      dataIndex: 'onlineStatus',
      render: (text: string) => {
        const des = FilmStatus[text + ''] || '';
        let color = 'red';

        if (des.indexOf('上线') > -1) {
          color = 'green';
        }

        const html = `<div style="width:15px;height:15px;border-radius: 50%;background:${color}" />`;

        return <div dangerouslySetInnerHTML={{ __html: html }} />;
      },
    },
    {
      title: '操作',
      dataIndex: 'action',
      render: (text, record) => {
        return <ActionButtons item={record} mutate={mutate} />;
      },
    },
  ];

  const formItem: QueryTableProps['formItem'] = [
    {
      name: 'title',
      label: '名称',
      renderCom: 'input',
      span: 4,
      comProps: {
        placeholder: '请输入片单名称',
      },
      itemProps: textShortItemLayout,
    },
    {
      name: 'listId',
      label: 'id',
      renderCom: 'input',
      span: 3,
      comProps: {
        placeholder: '请输入片单id',
      },
      itemProps: textShortItemLayout,
    },
    {
      name: 'creatorId',
      label: '发布账号',
      renderCom: 'input',
      span: 6,
      comProps: {
        placeholder: '请输入发布账号',
      },
      itemProps: textShortItemLayout,
    },
    {
      name: 'type',
      span: 4,
      label: '类型',
      renderCom: 'select',
      comProps: {
        options: FilmTypeOptionsMaps,
      },
      itemProps: textShortItemLayout,
    },
    {
      name: 'onlineStatus',
      span: 4,
      label: '状态',
      renderCom: 'select',
      comProps: {
        options: FilmStatusMaps,
      },
      itemProps: textShortItemLayout,
    },
    {
      name: 'visibility',
      span: 4,
      label: '隐私设置',
      renderCom: 'select',
      comProps: {
        options: VisiableOptionsMaps,
      },
    },
  ];

  const handleSearch = useCallback((params: any) => {
    const {
      pagination: page,
      query: { title, listId, creatorId, type, onlineStatus, visibility },
    } = params;
    let onlineStatusList = [];

    if (onlineStatus) {
      onlineStatusList = onlineStatus.split(',');
      onlineStatusList = onlineStatusList.map((item) => parseInt(item));
    }

    const request = {
      pagination: page,
      query: {
        title,
        listId,
        creatorId,
        type,
        onlineStatusList,
        visibility,
      },
      sort: {
        type: SORT_TYPE,
        key: 'create_time',
      },
    };

    setRequestBody(request);
  }, []);

  return (
    <div className={style.productionWrap}>
      <ShouCang
        visible={selectTimeVisible}
        setVisible={setSelectTimeVisible}
        item={targetProduction}
        mutate={mutate}
      />
      <QueryTable
        notQueryWhilestart={true}
        formItem={formItem}
        columns={columns}
        rowKey="listId"
        formRef={formRef}
        ref={queryTable}
        query={handleSearch}
        list={productionList}
        total={pagination?.total || 0}
        loading={isValidating}
        formItemLayout={formItemLayout}
        plusAction={
          <Link to="/content-manage/filmList-manage/add">
            <Button type="primary">添加新片单</Button>
          </Link>
        }
      />
    </div>
  );
};

export default withAuthorized(Production, [
  Role.SYSTEM,
  Role.WORKS,
  Role.COMMENT,
  Role.EXPERT,
]);
