import React, { useState, useCallback } from 'react';
import { Modal, Row, Col, InputNumber, message } from 'antd';
import { debounce } from '@/utils/util';
import { increaseFCountByListId } from '../services';

const ShouCang = (props) => {
  const { visible, setVisible, item = {}, mutate } = props;
  const { listId } = item;
  const [number, setNumber] = useState(0);

  const onOk = async () => {
    setVisible(false);
    console.log(number);
    await increaseFCountByListId({ listId, incrCount: number });
    mutate();
    message.success('增加成功');
  };

  const handleHidden = () => {
    setVisible(false);
  };

  const inputChangedDebounce = useCallback(
    debounce(
      (e) => {
        setNumber(e);
      },
      600,
      false
    ),
    []
  );

  return (
    <Modal title="添加收藏量"
      visible={ visible }
      onOk={ onOk }
      onCancel={ handleHidden }
    >
      <Row>
        <Col style={{ textAlign: 'right' }} span={ 6 }>
          收藏量：
        </Col>
        <Col span={ 18 }>{item.fCount || 0}</Col>
      </Row>
      <Row>
        <Col style={{ textAlign: 'right' }} span={ 6 }>
          添加：
        </Col>
        <Col span={ 18 }>
          <InputNumber min={ 1 }
            onChange={ inputChangedDebounce }
            style={{ width: '200px' }}
          />
        </Col>
      </Row>
    </Modal>
  );
};

export default ShouCang;
