import React, { useState, useCallback } from 'react';
import { Modal, Row, Col, Divider, Button, Popconfirm, message } from 'antd';
import { debounce } from '@/utils/util';
import { FilmStatus } from '@/utils/constant';
import { updateOnlineStatus } from '../services';
import { Link, history } from 'umi';

const ActionButtons = (props) => {
  const {
    item: { onlineStatus, listId },
    mutate,
  } = props;
  const online = FilmStatus[onlineStatus + ''].indexOf('上线') > -1;
  const showComment = () => {};

  const update = async () => {
    try {
      const res = await updateOnlineStatus({
        listId,
        onlineStatus: online ? 0 : 1,
      });

      mutate();
    } catch (error) {
      message.error('服务出错');
    }
  };
  const edit = () => {
    history.push(`/content-manage/filmList-manage/add?dramaId=${listId}`);
  };
  const text = online ? '下线' : '上线';

  return (
    // <Row gutter={ 6 }>
    //   {/* <Col span={ 8 }>
    //     <Button size="small" type="primary" ghost={ true } onClick={ showComment }>
    //       灌评
    //     </Button>
    //   </Col> */}
    //   <Col span={ 8 }>

    //   </Col>
    //   <Col span={ 8 }>

    //   </Col>
    // </Row>
    <div>
      <Popconfirm
        placement="topLeft"
        title={`确定${text}操作么？`}
        onConfirm={update}
        okText="确定"
        cancelText="取消"
      >
        <Button size="small">{text}</Button>
      </Popconfirm>
      <Divider type="vertical" />
      <Button
        size="small"
        type="primary"
        danger={true}
        ghost={true}
        onClick={edit}
      >
        修改
      </Button>
    </div>
  );
};

export default ActionButtons;
