// 更新作品截止时间 /wind-vane-backend/drama/operate/updateDramaTopDeadline
export function updateProductionTime(params: TopDeadlineInfo) {
  return request(
    `${ requestBasePath }/wingvate/wind-vane-backend/drama/operate/updateDramaTopDeadline?${ stringify(
      params
    ) }`
  );
}

// 获取指定作品评分变化 POST  /wind-vane-backend/statistics/public-score/fluctuate
export function useGetProductionCountChange(productionList: string[]) {
  const { data, isValidating } = useSWR(
    productionList?.length
      ? [
        `${ requestBasePath }/wingvate/wind-vane-backend/statistics/public-score/fluctuate`,
        productionList,
      ]
      : null,
    {
      requestParams: {
        method: 'POST',
        body: productionList,
      },
    }
  );

  return {
    productionCountChange: TransKvData(
      data?.data || [],
      'dramaId',
      'publicScoreFluctuate'
    ),
    countChangeLoading: isValidating,
  };
}

// 获取指定作品新增评论数 /wind-vane-backend/statistics/comment/growth-count
export function useGetProductionNewCount(productionList: string[]) {
  const { data, isValidating } = useSWR(
    productionList?.length
      ? [
        `${ requestBasePath }/wingvate/wind-vane-backend/statistics/comment/growth-count`,
        productionList,
      ]
      : null,
    {
      requestParams: {
        method: 'POST',
        body: productionList,
      },
    }
  );

  return {
    productionNewCount: TransKvData(
      data?.data || [],
      'dramaId',
      'commentGrowthCount'
    ),
    newCountLoading: isValidating,
  };
}

// 增加收藏量 /wind-vane-backend/list/operate/incrFollowTotal
export function increaseFCountByListId(params: any) {
  return request(
    `${ requestBasePath }/wingvate/wind-vane-backend/list/operate/incrFollowTotal`,
    {
      method: 'POST',
      body: params,
    }
  );
}

// 更新片单上下线状态
export function updateOnlineStatus(params: any) {
  return request(
    `${ requestBasePath }/wingvate/wind-vane-backend/list/operate/updateOnlineStatus`,
    {
      method: 'POST',
      body: params,
    }
  );
}
